@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Arabic&display=swap');

body{
    font-family: 'IBM Plex Sans Arabic', sans-serif;
}
*{
    padding:0;
    margin:0;
}
input[type=checkbox]{
	height: 0;
	width: 0;
	visibility: hidden;
}
label {
	cursor: pointer;
	text-indent: -9999px;
	width: 65px;
	height: 30px;
	background: grey;
	display: block;
	border-radius: 100px;
	position: relative;
}
label:after {
	content: '';
	position: absolute;
	top: 5px;
	left: 5px;
	width: 20px;
	height: 20px;
	background: #fff;
	border-radius: 90px;
	transition: 0.5s;
}
input:checked + label {
	background: black;
}
input:checked + label:after {
	left: calc(100% - 5px);
	transform: translateX(-100%);
}
label:active:after {
	width: 130px;
}

.app{
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap:20px;
}
.logo{
    margin-top: 30px;
}
.logo_img{
    border-radius: 50%;
    width:100px;
}
.button{
    display: flex;
    column-gap: 20%;
    align-items: center;
    justify-content: center;

}
.button_content .collect{
    margin-top: 20px;
    color:gray;

}
img{
    width:100%;
    max-width:500px;
    height: auto;
}
#noChange{
    margin-top: 30px;
    display: grid;
    grid-template-columns: repeat(1,1fr);
    margin-bottom: 0rem;
    transition: ease 0.5s;
   
}
#change{
    margin-top: 30px;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    margin-bottom: 0rem;
    transition: ease 0.5s;
}